interface Point {
    /**
     * Valid xValue of the chart
     *
     * @type {string|number}
     * @memberof Point
     */
    xValue?: string | number;

    /**
     * Valid seriesName for a chart
     *
     * @type {string}
     * @memberof Point
     */
    seriesName?: string;
}
class ChartingApi {
    charting;

    constructor(charting) {
        this.charting = charting;
    }

    /**
     * Allows users to programmatically highlight a point on the chart
     *
     * @memberof ChartingApi
     */
    highlight({ xValue, seriesName }: Point = { xValue: '', seriesName: '' }) {
        this.charting.chart.highlight(xValue, seriesName);
    }

    /**
     * Allows users to programmatically unhighlight all points on the chart
     *
     * @memberof ChartingApi
     */
    unHighlight() {
        this.charting.chart.unHighlight();
    }
}

export default ChartingApi;
